<template>
	<div>
		<van-nav-bar class="hader" title="热门分类" left-text="返回" left-arrow @click-left="back()">
			<a href="https://m.guanfan.top/#/app" slot="right" v-if="isiOS != true && isWeixin != true"><van-button type="primary" size="small">安装APP</van-button></a>
			<a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS == true && isWeixin != true">
				<van-button type="primary" size="small">安装APP</van-button>
			</a>
			<a href="https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzU0NTg5ODQzNA==&scene=124#wechat_redirect" slot="right" v-if="isWeixin == true">
				<van-button type="primary" size="small">关注</van-button>
			</a>
		</van-nav-bar>
		<div class="cont mue" style="padding: 0;margin-top: 46px;">
			<van-tabs>
				<van-tab v-for="item in sortList" :key="item.id" :title="item.classname">
					<div class="s-box" v-for="citem in item.childlist" :key="citem.id">
						<div class="s-item">{{ citem.classname }}</div>
						<div class="s-item-s" v-for="ccitem in citem.childlist" :key="ccitem.id">
							<div @click="navToSearch(ccitem.classname)">
								<div class="t-item">{{ ccitem.classname }}</div>
							</div>
						</div>
					</div>
				</van-tab>
			</van-tabs>
		</div>
	</div>
</template>

<script>
	import qs from 'qs';
	import axios from 'axios';
export default {
	data() {
		return {
			labelPosition: 'top',
			listpic: '?x-oss-process=style/list-img',
			userface: '?x-oss-process=style/user-img',
			detailpic: '?x-oss-process=style/detail-img',
			osshost: 'http://guanfan.oss-cn-hangzhou.aliyuncs.com/',
			// osshost: 'http://guanfan.oss-cn-hangzhou.aliyuncs.com/',
			sortList: '',
			citem: '',
			isWeixin: '',
			isiOS: ''
		};
	},
	created() {
		this.getClass();
		this.getwx();
		this.appSource();
	},
	methods: {
		getwx() {
			var ua = window.navigator.userAgent.toLowerCase();
			if (ua.match(/MicroMessenger/i) == 'micromessenger') {
				this.isWeixin = true;
			} else {
				this.isWeixin = false;
			}
		},
		appSource() {
			const u = navigator.userAgent,
				app = navigator.appVersion;
			if (!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
				this.isiOS = true;
			} else {
				this.isiOS = false;
			}
		},
		getClass() {
			// getSort()
			// 	.then(res => {
			// 		if(res.data.success){
			// 			this.sortList = res.data.result;
			// 		}
			// 	})
			// 	.catch(error => {
			// 		console.log(error);
			// 	});
				axios({
					method: 'post',
					url: '/index/class',
					headers: {
						'Content-Type': 'application/x-www-form-urlencoded'
					}
				}).then(response => {
					if(response.data.success){
						this.sortList = response.data.result;
					}
				});
		},
		navToSearch(key) {
			this.$router.push({ path: '/search', query: { key: key } });
		},
		back() {
			this.$router.go(-1); //返回上一层
		}
	}
};
</script>

<style>
.mue {
	width: 100%;
	height: calc(100vh - 46px);
	overflow: hidden;
	position: fixed;
}
.van-tabs--line .van-tabs__wrap {
	height: calc(100vh - 46px);
	width: 6rem;
	overflow: hidden;
	float: left;
}
.van-tabs__nav--line {
	line-height: 3rem;
	display: block;
}
.van-tabs__line {
	display: none;
}
.van-tab--active:before {
	position: absolute;
	content: ' ';
	left: 0;
	top: 0.3rem;
	z-index: 1;
	height: 2rem;
	background-color: #ee0a24;
	border-radius: 0.2rem;
	width: 0.2rem;
}
.van-tabs__content {
	float: left;
	height: calc(100vh - 46px);
	overflow: hidden;
	width: 16.5rem;
	display: block;
	position: relative;
	margin-left: 0.5rem;
	overflow-y: scroll;
	padding-bottom: 10rem;
}
.s-box {
	width: 100%;
	position: relative;
	overflow: hidden;
}
.van-tab__pane {
}
.s-item {
	width: 100%;
	margin: 0.3rem auto;
	text-align: center;
	padding: 0.5rem 0;
	border-radius: 0.3rem;
	font-size: 0.8rem;
}
.s-box:nth-child(1) .s-item {
	background: rgba(255, 216, 0, 0.1);
}
.s-box:nth-child(2) .s-item {
	background: rgba(112, 128, 255, 0.1);
}
.s-box:nth-child(3) .s-item {
	background: rgba(255, 68, 104, 0.1);
}
.s-box:nth-child(4) .s-item {
	background: rgba(255, 216, 0, 0.1);
}
.s-box:nth-child(5) .s-item {
	background: rgba(112, 128, 255, 0.1);
}
.s-box:nth-child(6) .s-item {
	background: rgba(255, 68, 104, 0.1);
}
.s-box:nth-child(7) .s-item {
	background: rgba(255, 216, 0, 0.1);
}
.s-box:nth-child(8) .s-item {
	background: rgba(112, 128, 255, 0.1);
}
.s-box:nth-child(9) .s-item {
	background: rgba(255, 68, 104, 0.1);
}
.s-item-s {
	width: 31%;
	float: left;
	margin: 0.3rem;
	margin-right: 0;
	text-align: center;
	padding: 0.5rem 0;
	border-radius: 0.3rem;
	font-size: 0.8rem;
	border: 0.05rem solid #eee;
}
</style>
